body{
    font-family: sans-serif;
    margin: 0 auto;
    padding: 25px;
    max-width: 400px;
    min-width: 400px;
    background-color: #dfe6e9;
    text-align: center;
}
.heading{
    text-align: center;
    font-size: 2em;
    color: #2d3436;
    margin: 20px 0 30px;
}
.card{
    border-radius: 25px;
    position: relative;
    padding: 25px 15px;
    background-color: #81ecec;
    margin: 50px 0;
    height: 200px;
    box-shadow: 0 2px 5px #ccc;
    text-align: left;

}
.top{
    border-radius: 25px 25px 0 0;
    height: 100px;
    width: 100%;
    background-color: #00cec9;
    position:absolute;

    top: 0;
    left: 0;
    display: flex;
}
.card img{
    margin-left: 60px;
    margin-top: 25px;

}
   
.circle-img{
    border-radius: 50%;
    border:7px solid #fff;
    
    width: 120px;
    height: 120px;
}
.name{
    margin-left: 25px;
    display: flex;
    font-size: 2em;
}
.bottom{
    margin-top: 120px;
}
.info{
    color: #1a7e76;
}